import React, { Component } from 'react';

function CartLineItem(props){

  const product = props.product;
  let isGiftWrap;

  const resizedImage = () => {
    let newurl = "";
    if(product.image.includes('.jpg')){
      let array = product.image.split('.jpg')
      newurl = array[0] + '_x300.jpg' + array[1]
    } else if (product.image.includes('.png')){ 
      let array = product.image.split('.png')
      newurl = array[0] + '_x300.png' + array[1]
    } else {
      newurl = product.image;
    }
    
    return newurl;
  }


  for (let prop in product.properties){
    prop === 'GiftTo' ? isGiftWrap = true : isGiftWrap = false
  }

  function increaseQuantity(){
    props.updateLineQuantity(props.product.variant_id, props.product.quantity + 1);
  };

  function decreaseQuantity(){
    props.updateLineQuantity(props.product.variant_id, props.product.quantity - 1);
  };

  function removeProduct(){
    props.updateLineQuantity(props.product.variant_id, 0);
  };

  function disableGiftWrapClick(e){
    console.log(product)
    if (isGiftWrap){
      e.preventDefault();
    }
  }
    return (
      <div className="Card Card--product">
        <a href={isGiftWrap ? "#" : product.url} onClick={disableGiftWrapClick}>
          <figure className="Card__figure">
              <img src={resizedImage()} alt={product.product_title} />
          </figure>
        </a>
        <div className="Card__details">
          <h6 className="Card__title">
            <a href={isGiftWrap ? "#" : product.url} onClick={disableGiftWrapClick}>
              <span>{product.product_title}</span> {product.variant_title && <small>Size: {product.variant_title}</small>}
            </a>
          </h6>
          <div className="quantity_and_amount">
            <div className="amount">${(product.line_price / 100).toFixed(2)}</div>
            <div className="quantity">

              <button onClick={decreaseQuantity}>
                <svg className="icon" width="13" height="1" viewBox="0 0 13 1">
                  <path fill="#3E2E29" fillRule="evenodd" d="M21.4114427,19.145076 L33.5939448,19.0428028 C33.8148577,19.0409482 33.9954464,19.2185301 33.997301,19.439443 C33.9973178,19.4414468 33.9973196,19.4434507 33.9973063,19.4454545 C33.995812,19.670696 33.8137958,19.8530331 33.5885573,19.854924 L21.4060552,19.9571972 C21.1851423,19.9590518 21.0045536,19.7814699 21.002699,19.560557 C21.0026822,19.5585532 21.0026804,19.5565493 21.0026937,19.5545455 C21.004188,19.329304 21.1862042,19.1469669 21.4114427,19.145076 Z" transform="translate(-21 -19)"/>
                </svg>
              </button>

              <span className="value">{product.quantity}</span>

              <button onClick={increaseQuantity}>
                <svg className="icon" width="13" height="13" viewBox="0 0 13 13">
                  <g fill="#3E2E29" fillRule="evenodd">
                    <path d="M0.411442717,6.14507598 L12.5939448,6.04280281 C12.8148577,6.04094823 12.9954464,6.21853007 12.997301,6.43944297 C12.9973178,6.44144678 12.9973196,6.4434507 12.9973063,6.44545455 C12.995812,6.67069602 12.8137958,6.85303312 12.5885573,6.85492402 L0.406055232,6.95719719 C0.185142339,6.95905177 0.00455361893,6.78146993 0.00269903586,6.56055703 C0.00268221363,6.55855322 0.00268044904,6.5565493 0.00269374223,6.55454545 C0.00418795874,6.32930398 0.186204226,6.14696688 0.411442717,6.14507598 Z"/>
                    <path d="M0.411442717,6.14507598 L12.5939448,6.04280281 C12.8148577,6.04094823 12.9954464,6.21853007 12.997301,6.43944297 C12.9973178,6.44144678 12.9973196,6.4434507 12.9973063,6.44545455 C12.995812,6.67069602 12.8137958,6.85303312 12.5885573,6.85492402 L0.406055232,6.95719719 C0.185142339,6.95905177 0.00455361893,6.78146993 0.00269903586,6.56055703 C0.00268221363,6.55855322 0.00268044904,6.5565493 0.00269374223,6.55454545 C0.00418795874,6.32930398 0.186204226,6.14696688 0.411442717,6.14507598 Z" transform="rotate(-90 6.5 6.5)"/>
                  </g>
                </svg>
              </button>

            </div>
          </div>
        </div>
      </div>
    );
  
}

export default CartLineItem;
