import React, { useRef, useState, useEffect, Fragment } from 'react';
import ReactDOM from 'react-dom';
import axios from 'axios';

import { component } from 'picoapp';
import { ReactDrawer } from '../parts';
import CartList from './CartList';

const getCartData = async () => {
  const response = await axios.get('/cart.js', { headers: { Pragma: 'no-cache' } });
  return response.data;
};

const MiniCartButton = ({ handleClick, open, itemCount }) => {
  return (
    <button className="Button Button--minicart" data-layout="noLayout" aria-label="Minicart" onClick={() => handleClick(!open)}>
        <svg viewBox='0 0 20 20' className='icon' width='1em' height='1em'>
          <use href='#icon-bag'></use>
          {itemCount > 0 ? <circle fill="#cc99a1" cx="10" cy="13" r="4" /> : null}
        </svg>
    </button>
  );
};

const MiniCart = (props) => {
  const [open, setOpen] = useState(false);
  const [cart, setCart] = useState({ items: [] });
  const [user, setUser] = useState({});
  const refDrawer = useRef(null);

  const updateLineItemQty = async (variant, qty) => {
    await axios
      .post('/cart/update.js', {
        updates: { [variant]: qty },
      })
      .then((response) => {
        setCart(response.data);
      })
      .catch((error) => {
        console.log(error);
      });
  };
  const API = {
    updateLineItemQty,
  };

  useEffect(() => {
    if(open){
      dataLayer.push({'event': 'optimize.activate'});
      refDrawer.current.focus();
      document.body.addEventListener('keyup', handleEscape);
    } else {
      document.body.removeEventListener('keyup', handleEscape);
    }
  }, [open])

  // Update the Cart Data on mounting
  useEffect(() => {
    getCartData().then((data) => {
      setCart(data);
    });
  }, []);

  //Close drawer on escape key
  function handleEscape(e) {
    if ( open && e.key === 'Escape'){
      setOpen(false);
    }
  }

  // Update cart and open when adding to cart
  useEffect(() => {
    props.ctx.on('cart.productAdded', () => {
      getCartData().then((data) => {
        setCart(data);
        setOpen(true);
        //document.getElementById('shopify-section-site-header').scrollIntoView();
      });
    });
  }, []);

  const handleClose = (e) => {
    if(e.type == "click"){
      setOpen(false)
    } else if (e.type == 'keyup' && e.key == "Enter"){
      setOpen(false)
    }
  } 

  return (
    <Fragment>
      <MiniCartButton handleClick={setOpen}t open={open} itemCount={cart.item_count}></MiniCartButton>
      <ReactDrawer showDrawer={open} className="Drawer--minicart" setShowDrawer={setOpen} position={'right'}>
        <div className="Minicart__content">
          <header className="Drawer__header">
            <h3 ref={refDrawer} tabIndex="0">Your Cart</h3>
            <button className="Button" type="button" data-layout="noLayout" onClick={handleClose}>
              <svg className="icon icon--primary" height="1em" viewBox="0 0 20 20" width="1em">
                <use xlinkHref="#icon-close"></use>
              </svg>
            </button>
          </header>
          <CartList cart={cart} products={cart.items} API={API} />
        </div>
      </ReactDrawer>
    </Fragment>
  );
};

export default component((node, ctx) => {
  const element = <MiniCart ctx={ctx} parentNode={node} />;
  ReactDOM.render(element, node);

  // Unmount component on Pjax navigation
  return function () {
    try {
      ReactDOM.unmountComponentAtNode(node);
    } catch (e) {
      console.error(e);
    }
  };
});